<template>
  <div class="login-container">
    <div class="background-wrapper">
      <!-- åœ¨æ­¤å¤„æ·»åŠ èƒŒæ™¯å›¾ç‰‡/è§†é¢‘ -->
      <img src="@/assets/login-bg.jpg" class="background-image" />
    </div>
    <el-card class="login-card">
      <div class="logo-wrapper">
        <img src="@/assets/æ•™è‚².png" class="logo" />
      </div>
      <h2 class="title">æ™ºæ…§é—®å­¦ç³»ç»Ÿ</h2>
      <h2>ç³»ç»Ÿç™»å½•</h2>
      <el-form :model="form" label-width="100px">
        <el-form-item label="ç”¨æˆ·ç±»å‹">
          <el-radio-group v-model="userType">
            <el-radio label="student">å­¦ç”Ÿ</el-radio>
            <el-radio label="teacher">æ•™å¸ˆ</el-radio>
            <el-radio label="admin">ç®¡ç†å‘˜</el-radio>
          </el-radio-group>
        </el-form-item>
        
        <el-form-item :label="userType === 'student' ? 'å­¦å·' : 'å·¥å·'">
          <el-input 
            v-model="form.username" 
            placeholder="è¯·è¾“å…¥æ‚¨çš„ç¼–å·"
        
          />
        </el-form-item>
        
        <el-form-item label="å¯†ç ">
          <el-input v-model="form.password" type="password" show-password/>
        </el-form-item>
        
        <el-form-item>
          <el-button type="primary" @click="handleLogin">ç™»å½•</el-button>
          <el-button @click="handleRegister">å¿«é€Ÿæ³¨å†Œ</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script setup>
import { ref } from 'vue'
import { useRouter } from 'vue-router'
import { studentLogin, teacherLogin ,adminLogin} from '@/api/auth'
import { ElMessage} from 'element-plus'

const router = useRouter()
const userType = ref('student')
const form = ref({
  username: '',
  password: ''
})

const handleLogin = async () => {
  try {
    let res
    if (userType.value === 'student') {
      res = await studentLogin({
        studentNumber: form.value.username,
        password: form.value.password
      })
    } else if (userType.value ==='admin') {
      res = await adminLogin({
        workerNumber: form.value.username,
        password: form.value.password
      })
    }
    else {
      res = await teacherLogin({
        workerNumber: form.value.username,
        password: form.value.password
      })
    }

    if (res.data.code !== 1) {
      throw new Error(res.data.msg || 'ç™»å½•å¤±è´¥')
    }

    // ä»…åœ¨ç™»å½•æˆåŠŸæ—¶å¤„ç†æ•°æ®å­˜å‚¨
    if (userType.value === 'student') {
      localStorage.setItem('student-token', res.data.data.token)
      localStorage.setItem('student-info', res.data.data.studentName)
      localStorage.setItem('student-id', res.data.data.id)
    }
    else if (userType.value ==='admin') {
      localStorage.setItem('admin-token', res.data.data.token)
      localStorage.setItem('admin-info', res.data.data.adminName)
      localStorage.setItem('admin-id', res.data.data.id)
    } 
    else {
      localStorage.setItem('teacher-token', res.data.data.token)
      localStorage.setItem('teacher-info', res.data.data.teacherName)
      localStorage.setItem('teacher-id', res.data.data.id)
    }

    router.push(localStorage.getItem('admin-token') ? '/system-model' : '/')
  } catch (error) {
    console.error('ç™»å½•å¤±è´¥:', error)
    ElMessage.error(error.message)
    // æ¸…ç©ºæ•æ„Ÿä¿¡æ¯
    form.value.password = ''
    form.value.username = ''
  }
}

// æ·»åŠ æ³¨å†Œè·³è½¬æ–¹æ³•
const handleRegister = () => {
  if (userType.value === 'admin') {
    ElMessage.error('ç®¡ç†å‘˜è´¦å·ä¸å¯è‡ªè¡Œæ³¨å†Œ')
    return
  }
  router.push(`/register?type=${userType.value}`)
}

// const validateNumber = () => {
