<template>
  <div class="chapter-container">
    <!-- è¯¾ç¨‹æ ‡é¢˜ -->
    <div class="header-section">
      <el-button 
        type="primary" 
        class="back-btn"
        icon="el-icon-arrow-left"
        @click="$router.push('/courses')"
      >
        è¿”å›è¯¾ç¨‹åˆ—è¡¨
      </el-button>
      <h2 class="course-title">
        {{ courseInfo.courseName }}
      </h2>
      <div class="teacher-info">
        <el-icon><User /></el-icon>
        <span class="teacher-name">ä»»è¯¾è€å¸ˆï¼š{{ courseInfo.teacherName }}</span>
      </div>
    </div>
    
    <!-- ç« èŠ‚åˆ—è¡¨ -->
    <el-card class="chapter-list">
      <el-row :gutter="20">
        <el-col 
          v-for="chapter in chapters" 
          :key="`chapter-${chapter.id}`"  
          :xs="24"
          :sm="12"
          :md="8"
          :lg="6"
        >
          <div class="chapter-link">
            <el-card
              class="chapter-card"
              shadow="hover"
              @click="handleChapterClick(chapter.id)"
            >
              <!-- åˆ é™¤ä»¥ä¸‹å¤é€‰æ¡†ä»£ç  -->
              <div class="chapter-order">
                ç¬¬{{ chapter.chapterOrder }}ç« 
              </div>
              <h3 class="chapter-name">
                {{ chapter.chapterName }}
              </h3>
              <el-progress 
                :percentage="(progressData[chapter.id]?.step1Complete || 0) * 33.33 + 
                            (progressData[chapter.id]?.step2Complete || 0) * 33.33 + 
                            (progressData[chapter.id]?.step3Complete || 0) * 33.33"
                :status="progressData[chapter.id]?.step3Complete === 1 ? 'success' : ''"
                style="margin-top:10px"
              />
            </el-card>
          </div>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>

<script>
import { 
  getCourseDetail, 
  getChapterProgress,
  getAllChapterProgress,
  getQuestionByParams,
  getChapterDetail // æ–°å¢æ¥å£å¯¼å…¥
} from '@/api/course'


export default {
  computed: {
    courseId() {
      return this.$route.params.courseId
    }
  },
  data() {
    return {
      selectedChapters: {},
      chapters: [],
      courseInfo: {},
      progressData: {} // æ–°å¢è¿›åº¦æ•°æ®
    }
  },
  mounted() {
    this.forceRefreshProgress() // è°ƒç”¨åˆ·æ–°æ–¹æ³•
     // åœ¨æ•°æ®åŠ è½½ååˆå§‹åŒ–å‹¾é€‰çŠ¶æ€
    this.$nextTick(() => {
      this.chapters.forEach(chapter => {
        this.$set(this.selectedChapters, chapter.id, false)
      })
    })
    // åœ¨mountedä¸­å¢åŠ 
    
  },
 // æ­£ç¡®æ”¾ç½®watchç›‘å¬
 watch: {
    '$route.params.courseId': {
      immediate: true,
      handler(newVal) {
        if (newVal) this.fetchAllChapterProgress()
      }
    }
  },
  methods: {
    // æ–°å¢è‡ªåŠ¨åˆ·æ–°æ–¹æ³•
  // æ–°å¢å¼ºåˆ¶åˆ·æ–°æ–¹æ³•
  async forceRefreshProgress() {
    await this.fetchCourseDetail();
    await this.fetchAllChapterProgress();
    this.chapters.forEach(chapter => {
      this.fetchProgress(chapter.id);
    });
  },
    // ç®€åŒ–ç« èŠ‚é€‰ä¸­é€»è¾‘
    handleCheckboxChange(chapter) {
      this.$store.commit('chapter/setSelectedChapter', {
        chapterId: chapter.id,
        isSelected: true
      });
    },
    
    // æ­£ç¡®çš„æ–¹æ³•å®šä¹‰å±‚çº§
    // åœ¨methodsä¸­æ·»åŠ 
    async fetchChapterDetail(chapterId) {
      try {
        const res = await getChapterDetail(chapterId) // éœ€è¦å…ˆåœ¨apiä¸­å®šä¹‰
        if (res.data.code === 1) {
          return res.data.data
        }
        return null
      } catch (error) {
        console.error('è·å–ç« èŠ‚è¯¦æƒ…å¤±è´¥:', error)
        this.$message.error('ç« èŠ‚è¯¦æƒ…åŠ è½½å¤±è´¥')
      }
    },
    
    async handleChapterClick(chapterId) {
      const controller = new AbortController();
      
      try {
        const [detailRes, questionData] = await Promise.all([
          getChapterDetail(chapterId, { signal: controller.signal }),
          this.fetchQuestion(chapterId)
        ]);
    
        const chapterData = detailRes.data.data;
        
        try {
          await this.$confirm(
            `å·²é€‰æ‹©ç¬¬${chapterData.chapterOrder}ç« `,
            'é€‰æ‹©æˆåŠŸ',
            {
              confirmButtonText: 'ç¡®å®š',
              cancelButtonText: 'å–æ¶ˆ',
              showCancelButton: true,
              type: 'success'
            }
          );
    
          // ç”¨æˆ·ç¡®è®¤åçš„é€»è¾‘
          this.$store.commit('chapter/setCurrentChapterParams', {
            userId: Number(localStorage.getItem('student-id')),
            courseId: this.courseId,
            chapterId,
            courseName: chapterData.courseName,
            chapterName: chapterData.chapterName,
            chapterOrder: chapterData.chapterOrder,
            questionData,
            progress: this.progressData[chapterId] || {}
          });
          // æ–°å¢æœ¬åœ°å­˜å‚¨è¯¾ç¨‹ID
          localStorage.setItem('currentCourseId', this.courseId);  // â† æ·»åŠ è¿™è¡Œ
          this.$root.setCurrentIds(this.courseId, chapterId);
          await this.fetchProgress(chapterId);
          const chapter = this.chapters.find(c => c.id === chapterId);
          this.handleCheckboxChange(chapter);
    
        } catch (confirmError) {
          if (confirmError === 'cancel') {
            controller.abort();
            this.$message.warning('æ“ä½œå·²å–æ¶ˆ');
            return;
          }
          throw confirmError;
        }
    
      } catch (error) {
        if (error.name === 'AbortError') {
          console.log('è¯·æ±‚å·²è¢«ä¸­æ­¢');
        } else {
          console.error('å¤„ç†å¤±è´¥:', error);
          this.$message.error(`æ“ä½œå¤±è´¥: ${error.message}`);
        }
      }
    },

    
    // ä¿®å¤æ–¹æ³•å®šä¹‰è¯­æ³•
    toggleChapterSelection(chapterId) {
      this.setSelectedChapter({
        chapterId,
        isSelected: !this.getSelectedChapters(chapterId)
      });
    },
    // æ–°å¢è·å–å…¨éƒ¨ç« èŠ‚è¿›åº¦æ–¹æ³•
    async fetchAllChapterProgress() {
      try {
        // ä¿®æ”¹ä¸ºä½¿ç”¨APIæ¨¡å—çš„è§„èŒƒè°ƒç”¨
        const res = await getAllChapterProgress({
          userId: Number(localStorage.getItem('student-id')),
          courseId: this.courseId
        });
        
        if (res.data.code === 1) {
          this.progressData = res.data.data.reduce((acc, curr) => {
            acc[curr.chapterId] = {
              step1Complete: Number(curr.step1Complete),
              step2Complete: Number(curr.step2Complete),
              step3Complete: Number(curr.step3Complete)
            };
            return acc;
          }, {});
           // æäº¤åˆ°Vuex store
           this.$store.commit('chapter/setChapterProgress', res.data.data);
        }
      } catch (error) {
        console.error('è·å–ç« èŠ‚è¿›åº¦å¤±è´¥:', error);
        this.$message.error('è¿›åº¦åŠ è½½å¤±è´¥'); // æ–°å¢é”™è¯¯æç¤º
      }
    },
    // æ–°å¢è¿›åº¦è·å–æ–¹æ³•
    async fetchProgress(chapterId) {
      try {
        const res = await getChapterProgress({
          userId: Number(localStorage.getItem('student-id')),
          courseId: this.courseId,
          chapterId: chapterId
        })
        // ç²¾ç¡®è§£æ„éœ€è¦çš„å­—æ®µ
        const { 
          step1Complete = 0, 
          step2Complete = 0, 
          step3Complete = 0 
        } = res.data?.data || {};
        
        // ä¿®æ”¹ä¸ºç›´æ¥èµ‹å€¼
        this.progressData[chapterId] = {
          step1Complete: Number(step1Complete),
          step2Complete: Number(step2Complete),
          step3Complete: Number(step3Complete)
        }
      } catch (error) {
        console.error('è¿›åº¦è·å–å¤±è´¥:', error)
      }
    },

    async fetchCourseDetail() {
      try {
        const res = await getCourseDetail(this.courseId)  // ç¡®ä¿æ­¤æ–¹æ³•è¢«è°ƒç”¨
        if (res.data.code === 1) {
          this.courseInfo = res.data.data
          this.chapters = res.data.data.chapters.sort((a, b) => a.chapterOrder - b.chapterOrder)
        }
      } catch (error) {
        console.error('è·å–è¯¾ç¨‹è¯¦æƒ…å¤±è´¥:', error)
        this.$message.error('è¯¾ç¨‹åŠ è½½å¤±è´¥')
      }
    },
    // åœ¨methodsä¸­æ·»åŠ è·å–é—®é¢˜æ–¹æ³•
    async fetchQuestion(chapterId) {
      try {
        const res = await getQuestionByParams({
          userId: Number(localStorage.getItem('student-id')),
          courseId: this.courseId,
          chapterId: chapterId
        });
        if (res.data.code === 1) {
          return res.data.data;
        }
        return null;
      } catch (error) {
        console.error('è·å–é—®é¢˜å¤±è´¥:', error);
        this.$message.error('é—®é¢˜åŠ è½½å¤±è´¥');
        return null;
      }
    }

    
   
  }
}
</script>

<style scoped>
.chapter-container {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.course-title {
  text-align: center;
  color: #303133;
  margin-bottom: 30px;
}

.chapter-list {
  margin-top: 20px;
}

.chapter-link {
  text-decoration: none;
}

.chapter-card {
  margin-bottom: 20px;
  transition: transform 0.3s;
  cursor: pointer;
}

.chapter-card:hover {
  transform: translateY(-3px);
}

.chapter-order {
  color: #409eff;
  font-size: 14px;
  margin-bottom: 8px;
}

.chapter-name {
  font-size: 16px;
  color: #606266;
  margin: 0;
}

.header-section {
  position: relative;
  margin-bottom: 30px;
  text-align: center;
}

.back-btn {
  position: absolute;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 18px;
  padding: 10px 20px;
  box-shadow: 0 2px 8px rgba(64, 158, 255, 0.2);
}

.back-btn:hover {
  transform: translateY(-50%) scale(1.05);
}

.teacher-info {
  margin-top: 15px;
  font-size: 16px;
  color: #606266;
  display: inline-flex;
  align-items: center;
  background: #f5f7fa;
  padding: 8px 20px;
  border-radius: 20px;
}

.teacher-info .el-icon {
  margin-right: 8px;
  color: #409eff;
}

.teacher-name {
  font-weight: 500;
}
.chapter-checkbox {
  position: absolute;
  right: 10px;
  top: 10px;
  z-index: 999;
  

}
</style>


